.container {
  padding: 20px;
  margin: 0 auto;
  background-color: transparent;
  border-radius: 8px;
}

.header {
  text-align: center;
  margin-bottom: 20px;
}

.header h1 {
  font-size: 24px;
  color: #333;
}

.tabs {
  display: flex;
  justify-content: center;
  margin-bottom: 20px;
  border-bottom: 1px solid #ddd;
}

.tab {
  padding: 10px 20px;
  cursor: pointer;
  background: none;
  border: none;
  font-size: 16px;
  color: #555;
}

.tab.active {
  font-weight: bold;
  color: #007bff;
  border-bottom: 2px solid #007bff;
}