.notificationListTab {
    padding: 20px;
    background: #fff;
    border-radius: 8px;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
    height: 80vh; /* Chiếm 90% chiều cao của viewport */
    overflow-y: auto; /* Cho phép cuộn dọc */
    scrollbar-width: thin; /* Đối với Firefox */
    scrollbar-color: transparent transparent; /* Đối với Firefox */
}

/* Ẩn thanh cuộn cho Chrome, Safari và Edge */
.notificationListTab::-webkit-scrollbar {
    width: 0; /* Ẩn thanh cuộn */
    background: transparent; /* Nền trong suốt */
}



