.createTab {
    padding: 20px;
    background: #fff;
    border-radius: 8px;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
    height: 80vh; /* Chiếm 90% chiều cao của viewport */
    overflow-y: auto; /* Cho phép cuộn dọc */
    scrollbar-width: thin; /* Đối với Firefox */
    scrollbar-color: transparent transparent; /* Đối với Firefox */
  }
  
  .formGroup {
    margin-bottom: 15px;
    box-sizing: border-box;
  }
  
  .label {
    display: block;
    margin-bottom: 5px;
    font-weight: bold;
  }
  
  .input,
  .textarea,
  .select {
    width: 100%;
    padding: 8px;
    border: 1px solid #ddd;
    border-radius: 4px;
    font-size: 14px;
    box-sizing: border-box;
  }
  
  .textarea {
    height: 100px;
    resize: vertical;
  }
  
  .actions {
    display: flex;
    gap: 10px;
    margin-top: 20px;
  }
  
  .previewBtn,
  .submitBtn,
  .closeBtn {
    padding: 10px 20px;
    border: none;
    border-radius: 4px;
    cursor: pointer;
  }
  
  .previewBtn {
    background: #6c757d;
    color: white;
  }
  
  .submitBtn {
    background: #007bff;
    color: white;
  }
  
  .submitBtn:disabled {
    background: #ccc;
    cursor: not-allowed;
  }
  
  .closeBtn {
    background: #dc3545;
    color: white;
  }