/* TargetSelectionComponent.module.css */

/* Định dạng cho các section (giới tính, khoa, khóa, ngành, lớp, target) */
.section {
    margin-bottom: 20px;
    padding: 15px;
    background-color: #ffffff;
    border-radius: 8px;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
}

/* Định dạng cho các tiêu đề h3 */
.title {
    font-size: 18px;
    margin-bottom: 10px;
    color: #007bff;
    border-bottom: 2px solid #007bff;
    padding-bottom: 5px;
}

/* Định dạng cho các label và checkbox */
.label {
    display: flex;
    align-items: center;
    margin-bottom: 10px;
    font-size: 16px;
    color: #555;
}

/* Định dạng cho container chứa các checkbox theo chiều ngang */
.checkbox-container {
    display: flex; /* Sử dụng Flexbox */
    flex-wrap: wrap; /* Cho phép xuống dòng nếu không đủ chỗ */
    gap: 15px; /* Khoảng cách giữa các checkbox */
}

/* Định dạng cho checkbox */
.checkbox {
    margin-right: 10px;
    width: 18px;
    height: 18px;
    cursor: pointer;
    accent-color: #007bff; /* Màu khi checkbox được chọn */
}

/* Định dạng cho phần hiển thị target */
.targetPre {
    background-color: #2c3e50;
    color: #ecf0f1;
    padding: 15px;
    border-radius: 8px;
    font-size: 14px;
    white-space: pre-wrap;
    word-wrap: break-word;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
}

/* Định dạng cho các checkbox "Tất cả" */
.allLabel {
    font-weight: bold;
    color: #007bff;
}

/* Hover effect cho label */
.label:hover {
    color: #007bff;
    transition: color 0.3s ease;
}